var React = require("react");
var ReactDOM = require("react-dom");
var LoggerClientControl = require('./LoggerClientControl.jsx');
var LoggerStore = require("../stores/LoggerStore")

class LoggerClientControlList extends React.Component {
	constructor(props) {
		super(props);

		var logState = LoggerStore.getLogState();

		this.state = {
			theList: [],
			hideInactive: LoggerStore.getHideInactiveState(),
			fileMode: this.props.fileMode,
			clientMode: LoggerStore.getClientMode(),
			shownCount: 0,
			applyToAllCategories: LoggerStore.getApplyToAllCategories(),
			initialClientStateDefault: LoggerStore.getInitialClientStateDefault()
		};

		this.loggerRegister = this.loggerRegister.bind(this);
		this.loggerUnregister = this.loggerUnregister.bind(this);
		this.getHideInactiveState = this.getHideInactiveState.bind(this);
		this.fileMode = this.fileMode.bind(this);
		this.changeClientMode = this.changeClientMode.bind(this);
		this.getAllShowClientState = this.getAllShowClientState.bind(this);
		this.clientModeButtonStyle = this.clientModeButtonStyle.bind(this);
		this.getApplyToAllCategories = this.getApplyToAllCategories.bind(this);
		this.getNewInitialClientStateDefault = this.getNewInitialClientStateDefault.bind(this);
	}

	componentWillMount() {
		LoggerStore.addEmitterListener("LoggerRegister", this.loggerRegister);
		LoggerStore.addEmitterListener("LoggerUnregister", this.loggerUnregister);
		LoggerStore.addEmitterListener("newHideInactiveState", this.getHideInactiveState);
		LoggerStore.addEmitterListener("FileMode", this.fileMode);
		LoggerStore.addEmitterListener("ChangeClientMode", this.changeClientMode);
		LoggerStore.addEmitterListener("newShowClientState", this.getAllShowClientState);
		LoggerStore.addEmitterListener("newApplyToAllCategories", this.getApplyToAllCategories);
		LoggerStore.addEmitterListener("newInitialClientStateDefault", this.getNewInitialClientStateDefault);
	}

	componentWillUnmount() {
		LoggerStore.removeEmitterListener("LoggerRegister", this.loggerRegister);
		LoggerStore.removeEmitterListener("LoggerUnregister", this.loggerUnregister);
		LoggerStore.removeEmitterListener("newHideInactiveState", this.getHideInactiveState);
		LoggerStore.removeEmitterListener("FileMode", this.fileMode);
		LoggerStore.removeEmitterListener("ChangeClientMode", this.changeClientMode);
		LoggerStore.removeEmitterListener("newShowClientState", this.getAllShowClientState);
		LoggerStore.removeEmitterListener("newApplyToAllCategories", this.getApplyToAllCategories);
		LoggerStore.removeEmitterListener("newInitialClientStateDefault", this.getNewInitialClientStateDefault);
	}

	loggerRegister(event) {
		var names = LoggerStore.getRegisteredClient();
		console.log("loggerRegister", JSON.stringify(names));
		this.setState({ theList: names });
		this.getAllShowClientState();
	}

	loggerUnregister(event) {
		var names = LoggerStore.getRegisteredClient();
		console.log("loggerUnregister", JSON.stringify(names));
		this.setState({ theList: names });
	}

	fileMode() {
		var fileModeState = LoggerStore.getFileMode();
		this.setState({ fileMode: fileModeState });
	}

	allToggle() {
		LoggerStore.toggleShowClientAll();
	}

	toggleHideInactive() {
		LoggerStore.toggleHideInactive();
	}

	getHideInactiveState() {
		var newState = LoggerStore.getHideInactiveState();
		this.setState({ hideInactive: newState });
		this.getAllShowClientState();
	}

	toggleAllToggle() {
		LoggerStore.toggleToggleAll();
	}

	default() {
		LoggerStore.defaultShowClientAll();
	}

	toggleApplyToAllCategories() {
		LoggerStore.toggleApplyToAllCategories();
	}

	getApplyToAllCategories() {
		var applyFlag = LoggerStore.getApplyToAllCategories();
		this.setState({ applyToAllCategories: applyFlag });
	}

	consoleMode() {
		console.log("consoleMode");
		LoggerStore.consoleMode();
	}

	devMode() {
		LoggerStore.devMode();
	}

	systemMode() {
		LoggerStore.systemMode();
	}

	perfMode() {
		LoggerStore.perfMode();
	}

	changeClientMode() {
		var newMode = LoggerStore.getClientMode();
		this.setState({ clientMode: newMode });
	}

	getAllShowClientState() {
		var allShowClientState = LoggerStore.getAllShowClientState();
		var hideInactive = LoggerStore.getHideInactiveState();

		var counter = 0;
		for (let client in allShowClientState) {
			if (allShowClientState.hasOwnProperty(client)) {
				if (allShowClientState[client] || !hideInactive) {
					counter++;
				}
			}
		}
		console.log("getAllShowClientState", counter);
		this.setState({ shownCount: counter });
	}

	allErrorsToggle() {
		LoggerStore.allErrorsToggle();
	}

	allWarningToggle() {
		LoggerStore.allWarningToggle();
	}

	allInfoToggle() {
		LoggerStore.allInfoToggle();
	}

	allLogsToggle() {
		LoggerStore.allLogsToggle();
	}

	allDebugToggle() {
		LoggerStore.allDebugToggle();
	}

	allVerboseToggle() {
		LoggerStore.allVerboseToggle();
	}

	allLocalConsoleToggle() {
		LoggerStore.allLocalConsoleToggle();
	}

	clientModeButtonStyle(mode) {
		if (mode === this.state.clientMode) {
			return "active";
		} else {
			return "inactive";
		}
		return theStyle;
	}


	errorsToggleInitialDefault() {
		LoggerStore.toggleInitialClientErrorsDefault();
	}

	warningsToggleInitialDefault() {
		LoggerStore.toggleInitialClientWarningsDefault();
	}

	infoToggleInitialDefault() {
		LoggerStore.toggleInitialClientInfoDefault();
	}

	logsToggleInitialDefault() {
		LoggerStore.toggleInitialClientLogsDefault();
	}

	debugToggleInitialDefault() {
		LoggerStore.toggleInitialClientDebugDefault();
	}

	verboseToggleInitialDefault() {
		LoggerStore.toggleInitialClientVerboseDefault();
	}

	localOnlyToggleInitialDefault() {
		LoggerStore.toggleInitialClientLocalOnlyDefault();
	}

	getNewInitialClientStateDefault() {
		var newInitialClientStateDefault = LoggerStore.getInitialClientStateDefault();
		this.setState({ initialClientStateDefault: newInitialClientStateDefault });
	}

	render() {
		var LoggerList = this.state.theList.map((name, index) => {
			return <LoggerClientControl key={index} clientName={name} fileMode={this.state.fileMode} />
		});

		return <div>
			<div className={'topDiv'}>

				<button className={'fsbl-logger-button'} type="button" onClick={this.allToggle}>Toggle All</button>
				{this.state.hideInactive ?
					<button className={'fsbl-logger-button active'} type="button" onClick={this.toggleHideInactive}>Show Inactive</button> :
					<button className={'fsbl-logger-button active'} type="button" onClick={this.toggleHideInactive}>Hide Inactive</button>
				}

				{this.state.fileMode ? // turn off certain buttons when in fileMode
					<span className={'fileStyle'}>{"File Mode"}</span>
					:
					<span className={'fsbl-logger-toggle-all-span'}>
						<button className={'fsbl-logger-button'} type="button" onClick={this.toggleAllToggle}>Toggle All Core</button>
						<button className={'fsbl-logger-button'} type="button" onClick={this.default}>Set All to Default</button>
					</span>
				}

				<span className={'divStyleGreen'}>
					<label>Default Capture State:</label>
					<label className={'paddingStyle'}>Errors</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Error} onChange={this.errorsToggleInitialDefault} />
					<label className={'paddingStyle'}>Warning</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Warn} onChange={this.warningsToggleInitialDefault} />
					<label className={'paddingStyle'}>Info</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Info} onChange={this.infoToggleInitialDefault} />
					<label className={'paddingStyle'}>Logs</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Log} onChange={this.logsToggleInitialDefault} />
					<label className={'paddingStyle'}>Debug</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Debug} onChange={this.debugToggleInitialDefault} />
					<label className={'paddingStyle'}>Verbose</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.Verbose} onChange={this.verboseToggleInitialDefault} />
					<label className={'paddingStyle'}>Local Console Only</label>
					<input className={'checkStyle'} type="checkbox" checked={this.state.initialClientStateDefault.LocalOnly} onChange={this.localOnlyToggleInitialDefault} />
				</span>
			</div>
			{!this.state.fileMode && this.state.shownCount === 0 ? null
				:

				<div className={"toggleDiv"}>
					<label className={'paddingStyle'}>Errors</label>
					<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allErrorsToggle}></button>
					<label className={'paddingStyle'}>Warning</label>
					<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allWarningToggle}></button>
					<label className={'paddingStyle'}>Info</label>
					<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allInfoToggle}></button>
					<label className={'paddingStyle'}>Logs</label>
					<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allLogsToggle}></button>
					<label className={'paddingStyle'}>Debug</label>
					<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allDebugToggle}></button>

					{this.state.clientMode === "console" ? null :
						<span>
							<label className={'paddingStyle'}>Verbose</label>
							<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allVerboseToggle}></button>
							<label className={'paddingStyle'}>Local Console Only</label>
							<button className={'button5Style fsbl-logger-button'} type="button" onClick={this.allLocalConsoleToggle}></button>
						</span>
					}
				</div>
			}

			<div className={'ulStyle'}>
				{LoggerList}
			</div>
		</div>
	}
};

module.exports = LoggerClientControlList;


