var RouterClient = require('../../../../clients/routerClientInstance');
var EventEmitter = require('events').EventEmitter;
var Utils = require("../../../../common/util");

// LoggerStore is the single React data store supporting the central logger. All communication with clients through router also happens here.
// This store support the pure React model, but Flux not needed (i.e. there is no Dispatcher).
// All changes of store are communicated back to components using the emitter; on receiving an emitter event components then call back to this store to get the new data.
// Actions triggered by components aren't broken out in a seperate class -- they are the "set" and "toggle" methods in the LoggerStore.
// Generally, all actions cause an emitter event to be sent (might a rare exception or two).

// There are lots of simularly named operations and functions in this store (due to the nature of the problem).  The easiest way to identify UI functionality is to start in
// the compponents, seeing what is controlled and map that behavior to the store operations.

function LoggerStore() {
	const STORAGE_KEY = "finsemble.loggerStore.mainkey";
	const HIGHLIGHT_COUNT = 4;
	var myEmitter = new EventEmitter();
	var messageCounter = 0;
	var firstMessageTime = Infinity;
	myEmitter.setMaxListeners(100);
	var logCallBack;
	var fileModeState = false;
	var registeredClientNames = [];
	var masterLog = [];
	var currentLog = [];
	var highlightString = ["", "", "", ""];
	var filterHighlights = false;
	var plainTextConsole = false;
	var clientState = {};
	var showClientState = {};
	var defaultShowClientState = false;
	var hideInactiveState = false;
	var windowWidth = 1170; // min width to hold buttons, etc
	var windowHeight = 700;
	var viewDepth = 3;
	var currentCategory = "system";
	var consoleModeState = true;
	var devModeState = true;
	var systemModeState = true;
	var perfModeState = true;
	var applyToAllCategories = true;

	var logState = { Error: true, Warn: true, Info: false, Log: false, Debug: false, Verbose: false };

	var initialClientStateDefault = { Error: true, Warn: true, Info: false, Log: false, Debug: false, Verbose: false, LocalOnly: false };

	var persistState = { // these are the state values that will persist
		logState,
		clientState,
		showClientState,
		plainTextConsole,
		filterHighlights,
		highlightString,
		hideInactiveState,
		viewDepth,
		currentCategory,
		consoleModeState,
		devModeState,
		systemModeState,
		perfModeState,
		applyToAllCategories,
		initialClientStateDefault
	};

	var storageData = localStorage.getItem(STORAGE_KEY);
	if (storageData) {
		persistState = JSON.parse(storageData);
		if (!persistState.highlightString) { // an empty array gets removed from JSON causing errors, so reinit to empty array
			persistState.highlightString = [];
		}
		console.log("LoggerStore: using presistent data", persistState);
	} else {
		console.log("LoggerStore: no presistent data");
	}

	var defaultClientState = {};
	defaultClientState.console = persistState.initialClientStateDefault;
	defaultClientState.dev = persistState.initialClientStateDefault;;
	defaultClientState.system = persistState.initialClientStateDefault;;
	defaultClientState.perf = persistState.initialClientStateDefault;;
	defaultClientState.clientChannel = "";

	var disableClientState = {};
	var disableClientConsoleSetting = { Error: false, Warn: false, Info: false, Log: false, Debug: false, Verbose: false };
	var disableClientSetting = { Error: false, Warn: false, Info: false, Log: false, Debug: false, Verbose: false, LocalOnly: false };
	disableClientState.console = disableClientConsoleSetting;
	disableClientState.dev = disableClientSetting;
	disableClientState.system = disableClientSetting;
	disableClientState.perf = disableClientSetting;
	disableClientState.clientChannel = "";


	this.initialize = function () {
		listenersInit();
	};

	this.getRegisteredClient = function () {
		return registeredClientNames;
	};

	this.getWindowWidth = function () {
		return windowWidth;
	};

	this.getWindowHeight = function () {
		return windowHeight;
	};

	this.showRawJSON = function () {
		myEmitter.emit('showRawJSON');
	};

	this.getPlainTextConsole = function () {
		return persistState.plainTextConsole;
	};

	this.getFileMode = function () {
		return fileModeState;
	};

	this.getCurrentLog = function () {
		return currentLog;
	};

	this.getLogCounts = function () {
		return { master: masterLog.length, display: currentLog.length };
	};

	this.getFirstMessageTime = function () {
		return firstMessageTime;
	};

	this.getViewDepth = function () {
		return persistState.viewDepth;
	};

	this.getConsoleModeState = function () {
		return persistState.consoleModeState;
	};

	this.getDevModeState = function () {
		return persistState.devModeState;
	};

	this.getSystemModeState = function () {
		return persistState.systemModeState;
	};

	this.getPerfModeState = function () {
		return persistState.perfModeState;
	};

	this.consoleModeToggle = function () {
		persistState.consoleModeState = !persistState.consoleModeState;
		updateCurrentLog();
		myEmitter.emit('ModeStateChange');
		myEmitter.emit('loggerNewMessages');
	};

	this.devModeToggle = function () {
		persistState.devModeState = !persistState.devModeState;
		updateCurrentLog();
		myEmitter.emit('ModeStateChange');
		myEmitter.emit('loggerNewMessages');
	};

	this.systemModeToggle = function () {
		persistState.systemModeState = !persistState.systemModeState;
		updateCurrentLog();
		myEmitter.emit('ModeStateChange');
		myEmitter.emit('loggerNewMessages');
	};

	this.perfModeToggle = function () {
		persistState.perfModeState = !persistState.perfModeState;
		updateCurrentLog();
		myEmitter.emit('ModeStateChange');
		myEmitter.emit('loggerNewMessages');
	};

	function noHighlightSet() {
		let highlighted = false;
		for (let h = 0; h < highlightString.length; h++) {
			if (persistState.highlightString[h].length > 0) {
				highlighted = true;
				break;
			}
		}
		return !highlighted;
	}

	// rebuild the current log based on the current state of the store
	function updateCurrentLog() {
		currentLog = [];
		for (var i = 0; i < masterLog.length; i++) {
			var type = masterLog[i].logType;
			var category = masterLog[i].category;
			var logClientName = masterLog[i].logClientName;
			if (persistState.logState[type] && persistState.showClientState[logClientName]) {
				const MAP = { "console":"consoleModeState", "dev":"devModeState", "system":"systemModeState", "perf":"perfModeState"};
				if (persistState[MAP[masterLog[i].category]]) {
					if (!persistState.filterHighlights || noHighlightSet()) {
						currentLog.push(masterLog[i]);
					} else {
						let highlighted = false;
						for (let h = 0; h < HIGHLIGHT_COUNT; h++) {
							highlighted = highlighted || masterLog[i].highlightFlag[h];
							if (highlighted) {
								currentLog.push(masterLog[i]);
								break;
							}
						}
					}
				}
			}
		}
		myEmitter.emit('loggerNewMessages');
	}

	function capitalizeFirstLetter(str) {
		return str.charAt(0).toUpperCase() + str.slice(1);
	}

	this.persist = function () {
		localStorage.setItem(STORAGE_KEY, JSON.stringify(persistState));
	};

	this.getClientState = function (clientName) {
		return persistState.clientState[clientName];
	};

	this.getLogState = function() {
		return persistState.logState;
	};

	this.getShowClientState = function (clientName) {
		return persistState.showClientState[clientName];
	};

	this.getAllShowClientState = function (clientName) {
		return persistState.showClientState;
	};

	this.consoleMode = function () {
		console.log("consoleMode");
		persistState.currentCategory = "console";
		myEmitter.emit('ChangeClientMode');
		myEmitter.emit('newClientState');
	};

	this.devMode = function () {
		persistState.currentCategory = "dev";
		myEmitter.emit('ChangeClientMode');
		myEmitter.emit('newClientState');
	};

	this.systemMode = function () {
		persistState.currentCategory = "system";
		myEmitter.emit('ChangeClientMode');
		myEmitter.emit('newClientState');
	};

	this.perfMode = function () {
		persistState.currentCategory = "perf";
		myEmitter.emit('ChangeClientMode');
		myEmitter.emit('newClientState');
	};

	this.getClientMode = function () {
		return persistState.currentCategory;
	};

	this.clearData = function () {
		masterLog = currentLog = [];
		myEmitter.emit('loggerNewMessages');
	};

	function testForHighlightInOneEntry(logEntry, searchString) {
		var result = false;
		if (searchString && searchString.length > 0) {
			for (var a = 0; a < logEntry.logArgs.length; a++) {
				if (logEntry.logArgs[a] && (typeof logEntry.logArgs[a]) === "string") {
					result = result || logEntry.logArgs[a].indexOf(searchString) !== -1;
					if (result) {
						break;
					}
				}
			}
		}
		return result;
	}

	function updateHighlightInMaster(searchString, highlightIndex) {
		for (var i = 0; i < masterLog.length; i++) {
			var result = testForHighlightInOneEntry(masterLog[i], searchString);
			masterLog[i].highlightFlag[highlightIndex] = result;
		}
	}

	this.setHighlight1 = function (searchString) {
		persistState.highlightString[0] = searchString;
		updateHighlightInMaster(searchString, 0);
		if (persistState.filterHighlights) {
			updateCurrentLog();
		} else {
			myEmitter.emit('NewHighlightValues1');
		}
	};

	this.setHighlight2 = function (searchString) {
		persistState.highlightString[1] = searchString;
		updateHighlightInMaster(searchString, 1);
		if (persistState.filterHighlights) {
			updateCurrentLog();
		} else {
			myEmitter.emit('NewHighlightValues2');
		}
	};

	this.setHighlight3 = function (searchString) {
		persistState.highlightString[2] = searchString;
		updateHighlightInMaster(searchString, 2);
		if (persistState.filterHighlights) {
			updateCurrentLog();
		} else {
			myEmitter.emit('NewHighlightValues3');
		}
	};

	this.setHighlight4 = function (searchString) {
		persistState.highlightString[3] = searchString;
		updateHighlightInMaster(searchString, 3);
		if (persistState.filterHighlights) {
			updateCurrentLog();
		} else {
			myEmitter.emit('NewHighlightValues4');
		}
	};

	this.setViewDepth= function (depth) {
		persistState.viewDepth = depth;
		myEmitter.emit('NewViewDepth');
	};

	this.setTop= function () {
		myEmitter.emit('SetTop');
	};

	this.setBottom= function () {
		myEmitter.emit('SetBottom');
	};

	this.getHighlightArray1 = function () {
		var highlightArray = currentLog.map(function (logEntry) {
			return logEntry.highlightFlag[0];
		});
		return highlightArray;
	};

	this.getHighlightArray2 = function() {
		var highlightArray = currentLog.map(function (logEntry) {
			return logEntry.highlightFlag[1];
		});
		return highlightArray;
	};

	this.getHighlightArray3 = function() {
		var highlightArray = currentLog.map(function (logEntry) {
			return logEntry.highlightFlag[2];
		});
		return highlightArray;
	};

	this.getHighlightArray4 = function() {
		var highlightArray = currentLog.map(function (logEntry) {
			return logEntry.highlightFlag[3];
		});
		return highlightArray;
	};

	this.getHighlightStrings = function() {
		return persistState.highlightString;
	};

	this.toggleFilterHighlights = function (alue) {
		persistState.filterHighlights = !persistState.filterHighlights;
		updateCurrentLog();
		myEmitter.emit('newFilterHighlights');
	};

	this.getFilterHighlightsState = function() {
		return persistState.filterHighlights;
	};

	this.getHideInactiveState = function () {
		return persistState.hideInactiveState;
	};

	this.toggleHideInactive = function() {
		persistState.hideInactiveState = !persistState.hideInactiveState;
		myEmitter.emit('newHideInactiveState');
	};

	this.getCurrentCategory = function (category) {
		return persistState.currentCategory;
	};

	this.toggleDisplayAll = function () {
		var logState = persistState.logState;
		// if any not true, then set them all to true; otherwise make them all false
		if (logState.Error && logState.Warn && logState.Info && logState.Log && logState.Debug && logState.Verbose) {
			persistState.logState = { Error: false, Warn: false, Info: false, Log: false, Debug: false, Verbose: false };
		} else {
			persistState.logState = { Error: true, Warn: true, Info: true, Log: true, Debug: true, Verbose: true };
		}
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayErrors = function() {
		persistState.logState.Error = !persistState.logState.Error;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayWarnings = function() {
		persistState.logState.Warn = !persistState.logState.Warn;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayInfo = function(alue) {
		persistState.logState.Info = !persistState.logState.Info;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayLogs = function() {
		persistState.logState.Log = !persistState.logState.Log;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayDebug = function() {
		persistState.logState.Debug = !persistState.logState.Debug;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	this.toggleDisplayVerbose = function() {
		persistState.logState.Verbose = !persistState.logState.Verbose;
		updateCurrentLog();
		myEmitter.emit('newDisplayState');
	};

	// not currently used
	this.togglePlainTextConsole = function() {
		persistState.plainTextConsole = !persistState.plainTextConsole;
		myEmitter.emit('newConsoleState');
	};

	this.toggleShowClient = function(clientName) {
		var showClientState = persistState.showClientState[clientName];
		showClientState = !showClientState;
		persistState.showClientState[clientName] = showClientState;
		updateCurrentLog();
		myEmitter.emit('newShowClientState');
	};

	this.toggleShowClientAll = function () {
		var allTrue = true;
		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client)) {
				allTrue = allTrue && persistState.showClientState[client];
				if (!allTrue) {
					break;
				}
			}
		}
		var toggledState = !allTrue;
		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client)) {
				persistState.showClientState[client] = toggledState;
			}
		}
		updateCurrentLog();
		myEmitter.emit('newShowClientState');
	};

	function showClientAllCategory(currentCategory) {
		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
				let clientState = persistState.clientState[client];
				let categoryState = Object.assign({}, defaultClientState[currentCategory]); // get a copy of the default category state
				clientState[currentCategory] = categoryState;
				RouterClient.transmit(clientState.clientChannel, clientState);
			}
		}
		myEmitter.emit('newClientState');
	};

	this.defaultShowClientAll = function () {
		if (persistState.applyToAllCategories) {
			showClientAllCategory("console");
			showClientAllCategory("dev");
			showClientAllCategory("system");
			showClientAllCategory("perf");
		} else {
			showClientAllCategory(persistState.currentCategory);
		}
		myEmitter.emit('newClientState');
	};

	// "toggle" all the values (i.e. one for each shown client) for the given category and type
	function allStateToggles(category, type) {
		var allTrue = true;
		var oneTrue = false;

		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
				let clientState = persistState.clientState[client];
				if (!(clientState[persistState.currentCategory][type])) {
					allTrue = false;
				}
				if ((clientState[persistState.currentCategory][type])) {
					oneTrue = true;
				}
			}
		}

		var toggledState = !(allTrue && oneTrue);

		// now gothough though all client states setting value for state type to toggledState
		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
				let clientState = persistState.clientState[client];
				let oldStateValue = clientState[persistState.currentCategory][type];
				clientState[persistState.currentCategory][type] = toggledState;

				// if value changed then let the client know
				if (oldStateValue !== toggledState) {
					persistState.clientState[client] = clientState;
					RouterClient.transmit(clientState.clientChannel, clientState);
				}
			}
		}
		myEmitter.emit('newClientState');
	}

	this.allErrorsToggle = function () {
		allStateToggles(persistState.currentCategory, "Error");
	};

	this.allWarningToggle = function () {
		allStateToggles(persistState.currentCategory, "Warn");
	};

	this.allInfoToggle = function () {
		allStateToggles(persistState.currentCategory, "Info");
	};

	this.allLogsToggle = function () {
		allStateToggles(persistState.currentCategory, "Log");
	};

	this.allDebugToggle = function () {
		allStateToggles(persistState.currentCategory, "Debug");
	};

	this.allVerboseToggle = function () {
		allStateToggles(persistState.currentCategory, "Verbose");
	};

	this.allLocalConsoleToggle = function () {
		allStateToggles(persistState.currentCategory, "LocalOnly");
	};

	function toggleAllCategory(currentCategory, specifiedToggleState) {
		var toggledState;

		if (arguments.length === 2) {
			toggledState = specifiedToggleState;
		} else {
			var allTrue = true;
			var oneTrue = false;
			for (let client in persistState.showClientState) {
				if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
					let clientState = persistState.clientState[client];
					if (!(clientState[currentCategory].Error
						&& clientState[currentCategory].Warn
						&& clientState[currentCategory].Info
						&& clientState[currentCategory].Log
						&& clientState[currentCategory].Debug)) {
						allTrue = false;
					}
					if (clientState[currentCategory].Error
						|| clientState[currentCategory].Warn
						|| clientState[currentCategory].Info
						|| clientState[currentCategory].Log
						|| clientState[currentCategory].Debug) {
						oneTrue = true;
					}
				}
			}
			toggledState = !(allTrue && oneTrue);
		}

		for (let client in persistState.showClientState) {
			if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
				let clientState = persistState.clientState[client];
				if (toggledState) {
					clientState[currentCategory] =
					{
						Error: true, Warn: true, Info: true, Log: true, Debug: true,
						Verbose: clientState[currentCategory].Verbose,
						LocalOnly: clientState[currentCategory].LocalOnly
					};
				} else {
					clientState[currentCategory] =
					{
						Error: false, Warn: false, Info: false, Log: false, Debug: false,
						Verbose: clientState[currentCategory].Verbose,
						LocalOnly: clientState[currentCategory].LocalOnly
					};
				}
				persistState.clientState[client] = clientState;
				RouterClient.transmit(clientState.clientChannel, clientState);
			}
		}
		return toggledState;
	};

	this.toggleToggleAll = function () {
		if (persistState.applyToAllCategories) {
			var mainToggleState = toggleAllCategory(persistState.currentCategory); // use the result of the current category to set all
			toggleAllCategory("console", mainToggleState);
			toggleAllCategory("dev", mainToggleState);
			toggleAllCategory("system", mainToggleState);
			toggleAllCategory("perf", mainToggleState);
		} else {
			toggleAllCategory(persistState.currentCategory);
		}
		myEmitter.emit('newClientState');
	};

	this.toggleApplyToAllCategories = function () {
		persistState.applyToAllCategories = !persistState.applyToAllCategories;
		myEmitter.emit('newApplyToAllCategories');
	};

	this.getApplyToAllCategories = function () {
		return persistState.applyToAllCategories;
	};

	function toggleClientAllCategory(clientName, currentCategory, specifiedToggleState, transmit) {
		var clientState = persistState.clientState[clientName]; // get state for all categories

		if (arguments.length === 4) {
			clientState[currentCategory] = specifiedToggleState; // use incoming state to set current category
		} else {
			// if any not true, then set virtually all to true in current catgory; otherwise set virtually all to false
			if (clientState[currentCategory].Error
				&& clientState[currentCategory].Warn
				&& clientState[currentCategory].Info
				&& clientState[currentCategory].Log
				&& clientState[currentCategory].Debug) {
				clientState[currentCategory] =
				{
					Error: false, Warn: false, Info: false, Log: false, Debug: false,
					Verbose: clientState[currentCategory].Verbose,
					LocalOnly: clientState[currentCategory].LocalOnly
				};
			} else {
				clientState[currentCategory] =
				{
					Error: true, Warn: true, Info: true, Log: true, Debug: true,
					Verbose: clientState[currentCategory].Verbose,
					LocalOnly: clientState[currentCategory].LocalOnly
				};
			}
		}
		persistState.clientState[clientName] = clientState;
		if (transmit) {
			RouterClient.transmit(clientState.clientChannel, clientState);
		}
		return clientState[currentCategory];
	}

	this.toggleClientAll = function (clientName) {
		if (persistState.applyToAllCategories) {
			var mainToggleState = toggleClientAllCategory(clientName, persistState.currentCategory, false); // use the result of the current category to set all
			console.log("mainToggleState", clientName, persistState.currentCategory, mainToggleState);
			toggleClientAllCategory(clientName, "console", mainToggleState, false);
			toggleClientAllCategory(clientName, "dev", mainToggleState, false);
			toggleClientAllCategory(clientName, "system", mainToggleState, false);
			toggleClientAllCategory(clientName, "perf", mainToggleState, true);
		} else {
			toggleClientAllCategory(clientName, persistState.currentCategory, true);
		}
		myEmitter.emit('newClientState');
	};


	function toggleClientStateAllCategories(clientState, type) {
		clientState["console"][type] = !clientState["console"][type];
		clientState["dev"][type] = !clientState["dev"][type];
		clientState["system"][type] = !clientState["system"][type];
		clientState["perf"][type] = !clientState["perf"][type];
	}

	this.toggleClientErrors = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Error");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientWarnings = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Warn");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientInfo = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Info");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientLogs = function (clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Log");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientDebug = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Debug");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientVerbose = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "Verbose");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleClientLocalOnly = function(clientName) {
		var clientState = persistState.clientState[clientName];
		toggleClientStateAllCategories(clientState, "LocalOnly");
		RouterClient.transmit(clientState.clientChannel, clientState);
		myEmitter.emit('newClientState');
	};

	this.toggleInitialClientErrorsDefault = function() {
		persistState.initialClientStateDefault.Error = !persistState.initialClientStateDefault.Error;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientWarningsDefault = function() {
		persistState.initialClientStateDefault.Warn = !persistState.initialClientStateDefault.Warn;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientInfoDefault = function() {
		persistState.initialClientStateDefault.Info = !persistState.initialClientStateDefault.Info;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientLogsDefault = function () {
		persistState.initialClientStateDefault.Log = !persistState.initialClientStateDefault.Log;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientDebugDefault = function() {
		persistState.initialClientStateDefault.Debug = !persistState.initialClientStateDefault.Debug;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientVerboseDefault = function() {
		persistState.initialClientStateDefault.Verbose = !persistState.initialClientStateDefault.Verbose;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.toggleInitialClientLocalOnlyDefault = function() {
		persistState.initialClientStateDefault.LocalOnly = !persistState.initialClientStateDefault.LocalOnly;
		myEmitter.emit('newInitialClientStateDefault');
	};

	this.getInitialClientStateDefault = function () {
		return persistState.initialClientStateDefault;
	};

	this.closeChildren = function () {
		myEmitter.emit('closeDisplayChildren');
	};

	this.saveToFile = function () {
		var data = { masterLog, persistState, registeredClientNames };
		var json = JSON.stringify(data);
		var blob = new Blob([json], {type: "application/json"});
		var url  = URL.createObjectURL(blob);

		this.tempDoc = window.open("about:blank", null, "location=1,toolbar=1,menubar=1,resizable=1,width=150,height=150");
		var doc = this.tempDoc.document;
		var a = doc.createElement('a');
		a.download    = "FinsembleLog.json";
		a.href        = url;
		a.textContent = "";
		doc.open("text/html");
		doc.write("<div id='content'></div>");
		doc.getElementById('content').appendChild(a);
		a.click();
		doc.close();
		this.tempDoc.close();
	};

	// enables fileMode, essentially disabling all external communication with clients in order to read log data from a file...a special case.
	this.fileMode = function () {

		fileModeState = true;

		var element = document.createElement('div');
		element.innerHTML = '<input type="file">';
		var fileInput = element.firstChild;

		fileInput.addEventListener('change', function() {
			var file = fileInput.files[0];

			if (file.name.match(/\.(json)$/)) {
				var reader = new FileReader();

				reader.onload = function () {
					console.log("***** FileMode *****");

					var data = JSON.parse(reader.result);

					for (let client in persistState.showClientState) {
						if (persistState.showClientState.hasOwnProperty(client) && (!persistState.hideInactiveState || persistState.showClientState[client])) {
							let clientState = Object.assign({}, disableClientState); // get a copy of the disabled state
							clientState.clientChannel = persistState.clientState[client].clientChannel; // don't overwrite the channel name
							RouterClient.transmit(clientState.clientChannel, clientState);
							console.debug("disabledClientState", clientState.clientChannel, clientState);
						}
					}

					masterLog = data.masterLog;
					persistState = data.persistState;
					registeredClientNames = data.registeredClientNames;

					console.log("ImportFile", data, file, reader);

					updateCurrentLog();

					myEmitter.emit('FileMode');
					myEmitter.emit('LoggerRegister');
					myEmitter.emit('newFilterHighlights');
					myEmitter.emit('newDisplayState');
					myEmitter.emit('LoggerUnregister');
					myEmitter.emit('newShowClientState');
				};

				reader.readAsText(file);
			} else {
				console.error("File not supported, .txt or .json files only");
			}
		});

		fileInput.click();
	};

	// all incoming messages from clients are processed here
	function processMessage(message) {
		var newMessage = {};
		var parsedLogData;
		if (messageCounter++ < 1000 && (message.logTimestamp < firstMessageTime)) { // look for the lowest time value in early messages to set base time
			firstMessageTime = message.logTimestamp;
		}
		newMessage.logClientName = message.logClientName;
		newMessage.category = message.category;
		newMessage.logType = message.logType;
		newMessage.logTimestamp = message.logTimestamp;
		newMessage.highlightFlag= [];
		newMessage.logArgs = [];
		newMessage.parsedLogArgs = [];
		newMessage.allLogArgs = "";

		try {
			parsedLogData = JSON.parse(message.logData);
		} catch (err) {
			parsedLogData = { Note: "*** LoggingService: error parsing data (see local console warning for data) ***" };
			console.warn(parsedLogData, message.logData);
		}

		var counter = 1;
		var firstArg = true;
		for (var property in parsedLogData) { // don't know how many arguments passed in to console output
			if (parsedLogData.hasOwnProperty(property)) {
				newMessage.parsedLogArgs.push(parsedLogData[property]);
				let argString = JSON.stringify(parsedLogData[property]);
				newMessage.logArgs.push(argString);
				if (firstArg) {
					newMessage.allLogArgs = argString + " ";
					firstArg = false;
				} else {
					newMessage.allLogArgs = newMessage.allLogArgs + argString + " ";
				}
			}
		}
		for (let h = 0; h < HIGHLIGHT_COUNT; h++) {
			let state = testForHighlightInOneEntry(newMessage, persistState.highlightString[h]);;
			newMessage.highlightFlag[h] = state;
		}
		return newMessage;
	}

	this.addEmitterListener = function(event, callback) {
		myEmitter.on(event, callback);
	};

	this.removeEmitterListener = function(event, callback) {
		myEmitter.removeListener(event, callback);
	};

	function findIndexByTimestamp(log, timestamp) {
		var index;
		for (var i = log.length - 1; i > 0; i--) {
			if (log[i].logTimestamp < timestamp) {
				break;
			}
		}
		return i;
	}

	// this sorts only the end of the table....when getting a new buffer of messages, only need to sort from the earliest timestamp forward;
	// the existing buffer (before new buffer is added) may have older timestamps, which is why must start from earliest timestamp in new messages
	function sortNewLogMessages(fromTimestamp) {

		// compare function to sort messages by timestamp
		function compareTimestamps(a, b) {
			var result = 0;
			if (a.logTimestamp < b.logTimestamp) {
				result = -1;
			}
			if (a.logTimestamp > b.logTimestamp) {
				result = 1;
			}
			return result;
		}

		var indexToSortFrom = findIndexByTimestamp(masterLog, fromTimestamp);
		var newLogPortion = masterLog.splice(indexToSortFrom, masterLog.length);
		newLogPortion.sort(compareTimestamps);
		masterLog = masterLog.concat(newLogPortion);
	}

	// called whenever the Logger's window size is changed
	function processSizeChange(width, height) {
		if (windowWidth !== width) {
			windowWidth = width;
			myEmitter.emit('WindowResize');
			myEmitter.emit('newShowClientState'); // don't know why but table size chance alone doesn't refresh table, so sent this too to trigger refresh
		}
		if (windowHeight !== height) { // the height isn't fully tracked because less important
			windowHeight = height;
			myEmitter.emit('WindowResize');
			myEmitter.emit('newShowClientState'); // don't know why but table size chance alone doesn't refresh table, so sent this too to trigger refresh
		}
	}

	function listenersInit() {

		var finWindow = fin.desktop.Window.getCurrent();
		finWindow.addEventListener("bounds-changed", function (event) {
			processSizeChange(event.width, event.height);
		});

		finWindow.addEventListener("maximized", function (event) {
			processSizeChange(finWindow.nativeWindow.outerWidth, finWindow.nativeWindow.outerHeight);
		});

		finWindow.addEventListener("restored", function (event) {
			processSizeChange(finWindow.nativeWindow.outerWidth, finWindow.nativeWindow.outerHeight);
		});

		RouterClient.addListener("logger.service.logMessages", function logCallBack(error, message) {
			if (!fileModeState) { // typically not in filemode, so go through normal handling of log messages, but ignore messages in file mode
				var earliestTimestamp = Infinity;
				var messageArray = message.data;
				for (var i = 0; i < messageArray.length; i++) {
					var newMessage = processMessage(messageArray[i]);
					if (newMessage.logTimestamp < earliestTimestamp) {
						earliestTimestamp = newMessage.logTimestamp;
					}
					masterLog.push(newMessage);
				}
				sortNewLogMessages(earliestTimestamp);
				updateCurrentLog();
			}
		});

		RouterClient.addResponder("logger.service.register", function (error, queryMessage) {
			console.log("logger.service.register", queryMessage.data);
			var clientName = queryMessage.data.clientName;

			if (!fileModeState) { // typically not in filemode, so go through normal registration
				if (!registeredClientNames.includes(clientName)) {
					registeredClientNames.push(clientName);
				}

				if (!(clientName in persistState.showClientState)) {
					persistState.showClientState[clientName] = defaultShowClientState;
				}

				// the data will from from persistState, but if not in persistState, add it using default values
				let newClientState = Utils.clone(defaultClientState);
				newClientState.clientChannel = queryMessage.data.clientChannel;
				if (!(clientName in persistState.clientState)) {
					persistState.clientState[clientName] = newClientState;
				}

				var debugState = persistState.clientState[clientName];
				console.log("logger.service.register Response", clientName, debugState);
				queryMessage.sendQueryResponse(null, debugState);

				myEmitter.emit('LoggerRegister');

			} else { // ignore registration if in filemode -- send back disabled state
				console.debug("Registration in FileMode");
				var newClientState = Object.assign({}, disableClientState);
				newClientState.clientChannel = queryMessage.data.clientChannel;
				queryMessage.sendQueryResponse(null, newClientState);
			}

		});
		RouterClient.addResponder("logger.service.unregister", function (error, queryMessage) {
			console.log("logger.service.unregister", queryMessage.data);
			var clientName = queryMessage.data.clientName;
			for (var i = 0; i < registeredClientNames.length; i++) {
				if (registeredClientNames[i] === clientName) {
					registeredClientNames.splice(i, 1);
				}
			}
			myEmitter.emit('LoggerUnregister');
		});
	};
}

var loggerStore = new LoggerStore();

//wait for Router to be ready before initializing.
RouterClient.ready(function(){
	loggerStore.initialize();
});

module.exports = loggerStore;



