var React = require("react");
var ReactDOM = require("react-dom");
var LoggerClientControl = require('./LoggerClientControl.jsx');
var LoggerStore = require("../stores/LoggerStore")

// component for what is displayed locally in central logger
class LoggerClientControlList extends React.Component {
	constructor(props) {
		super(props);

		var initialLogState = LoggerStore.getLogState();
		
		this.state = {
			hightLight1: LoggerStore.getHighlightStrings()[0],
			hightLight2: LoggerStore.getHighlightStrings()[1],
			hightLight3: LoggerStore.getHighlightStrings()[2],
			hightLight4: LoggerStore.getHighlightStrings()[3],
			viewDepth: LoggerStore.getViewDepth(),
			filterHighlights: LoggerStore.getFilterHighlightsState(),
			plainTextConsole: LoggerStore.getPlainTextConsole(),
			logState: initialLogState,
			consoleMode:  LoggerStore.getConsoleModeState(), 
			devMode: LoggerStore.getDevModeState(),
			systemMode: LoggerStore.getSystemModeState(),
			perfMode: LoggerStore.getPerfModeState()
		};

		this.hightLight1 = this.hightLight1.bind(this);
		this.hightLight2 = this.hightLight2.bind(this);
		this.hightLight3 = this.hightLight3.bind(this);
		this.hightLight4 = this.hightLight4.bind(this);
		this.getNewDisplayState = this.getNewDisplayState.bind(this);
		this.getPlainTextConsoleState = this.getPlainTextConsoleState.bind(this);
		this.filterHightlightsToggle = this.filterHightlightsToggle.bind(this);
		this.getNewFilterHighlights = this.getNewFilterHighlights.bind(this);
		this.viewDepth = this.viewDepth.bind(this);
		this.getNewModeState = this.getNewModeState.bind(this);
	}

	componentWillMount() {
		LoggerStore.addEmitterListener("newDisplayState", this.getNewDisplayState);
		LoggerStore.addEmitterListener("newConsoleState", this.getPlainTextConsoleState);
		LoggerStore.addEmitterListener("newFilterHighlights", this.getNewFilterHighlights);
		LoggerStore.addEmitterListener("ModeStateChange", this.getNewModeState);
	}

	componentWillUnmount() {
		LoggerStore.removeEmitterListener("newDisplayState", this.getNewDisplayState);
		LoggerStore.removeEmitterListener("newConsoleState", this.getPlainTextConsoleState);
		LoggerStore.removeEmitterListener("newFilterHighlights", this.getNewFilterHighlights);
		LoggerStore.removeEmitterListener("ModeStateChange", this.getNewModeState);
	}

	getNewModeState() {
		var newConsoleMode = LoggerStore.getConsoleModeState();
		var newDevMode = LoggerStore.getDevModeState();
		var newSystemMode = LoggerStore.getSystemModeState();
		var newPerfMode = LoggerStore.getPerfModeState();

		this.setState({ consoleMode: newConsoleMode });
		this.setState({ devMode: newDevMode });
		this.setState({ systemMode: newSystemMode });
		this.setState({ perfMode: newPerfMode });
	}

	getNewDisplayState() {
		var newLogState = LoggerStore.getLogState();
		this.setState({ logState: newLogState });
	}

	getPlainTextConsoleState() {
		var plainTextConsoleState = LoggerStore.getPlainTextConsole();
		this.setState({ plainTextConsole: plainTextConsoleState });
	}

	hightLight1(event) {
		this.setState({ hightLight1: event.target.value });
		LoggerStore.setHighlight1(event.target.value);
	}

	hightLight2(event) {
		this.setState({ hightLight2: event.target.value });
		LoggerStore.setHighlight2(event.target.value);
	}

	hightLight3(event) {
		this.setState({ hightLight3: event.target.value });
		LoggerStore.setHighlight3(event.target.value);
	}

	hightLight4(event) {
		this.setState({ hightLight4: event.target.value });
		LoggerStore.setHighlight4(event.target.value);
	}

	viewDepth(event) {
		this.setState({ viewDepth: event.target.value });
		LoggerStore.setViewDepth(event.target.value);
	}

	top(event) {
		LoggerStore.setTop();
	}

	bottom(event) {
		LoggerStore.setBottom();
	}

	filterHightlightsToggle() {
		LoggerStore.toggleFilterHighlights();
	}

	getNewFilterHighlights() {
		var newState = LoggerStore.getFilterHighlightsState();
		this.setState({ filterHighlights: newState });
	}

	allToggle() {
		LoggerStore.toggleDisplayAll();
	}
	
	errorsToggle() {
		LoggerStore.toggleDisplayErrors();
	}

	warningsToggle() {
		LoggerStore.toggleDisplayWarnings();
	}

	infoToggle() {
		LoggerStore.toggleDisplayInfo();
	}

	logsToggle() {
		LoggerStore.toggleDisplayLogs();
	}

	debugToggle() {
		LoggerStore.toggleDisplayDebug();
	}

	verboseToggle() {
		LoggerStore.toggleDisplayVerbose();
	}

	closeChildren() {
		LoggerStore.closeChildren();
	}

	showRawJSON() {
		LoggerStore.showRawJSON();
	}

	plainTextConsoleToggle() {
		LoggerStore.togglePlainTextConsole();
	}

	consoleModeToggle() {
		LoggerStore.consoleModeToggle();
	}

	devModeToggle() {
		LoggerStore.devModeToggle();
	}

	systemModeToggle() {
		LoggerStore.systemModeToggle();
	}

	perfModeToggle() {
		LoggerStore.perfModeToggle();
	}

	render() {

		return <div>
			<div className={'topDiv'}>
				<label className={'sepStyle'}>Highlight</label>
				<label>1: </label>
				<input className={'inputStyle1'} type="text" value={this.state.hightLight1} onChange={this.hightLight1} />
				<label className={'sepStyle'}></label>
				<label>2: </label>
				<input className={'inputStyle2'} type="text" value={this.state.hightLight2} onChange={this.hightLight2} />
				<label className={'sepStyle'}></label>
				<label>3: </label>
				<input className={'inputStyle3'} type="text" value={this.state.hightLight3} onChange={this.hightLight3} />
				<label className={'sepStyle'}></label>
				<label>4: </label>
				<input className={'inputStyle4'} type="text" value={this.state.hightLight4} onChange={this.hightLight4} />
				<label className={'spacer1'}></label>
				
				<span>&nbsp;</span><input type="checkbox" checked={this.state.filterHighlights} onChange={this.filterHightlightsToggle} /> <label>Filter</label>
			</div>
			<div className={'topDiv'}>
				
				<button className={'fsbl-logger-button'} type="button" onClick={this.allToggle}>Toggle</button>
				<span>&nbsp;</span>
				<label className={'allErrorStyle'}>Errors</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Error} onChange={this.errorsToggle} />
				<label className={'paddingStyle'}>Warning</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Warn} onChange={this.warningsToggle} />
				<label className={'paddingStyle'}>Info</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Info} onChange={this.infoToggle} />
				<label className={'paddingStyle'}>Logs</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Log} onChange={this.logsToggle} />
				<label className={'paddingStyle'}>Debug</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Debug} onChange={this.debugToggle} />
				<label className={'paddingStyle'}>Verbose</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.logState.Verbose} onChange={this.verboseToggle} />
			
				<div className={"h-separator"} />
				<label className={'modeStyle'}>Console</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.consoleMode} onChange={this.consoleModeToggle} />
				<label className={'modeStyle'}>Dev</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.devMode} onChange={this.devModeToggle} />
				<label className={'modeStyle'}>System</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.systemMode} onChange={this.systemModeToggle} />
				<label className={'modeStyle'}>Perf</label>
				<input className={'checkStyle'} type="checkbox" checked={this.state.perfMode} onChange={this.perfModeToggle} />

				<div className={"h-separator"} />
				<button className={'fsbl-logger-button'} type="button" onClick={this.top}>Top</button>
				<button className={'fsbl-logger-button'} type="button" onClick={this.bottom}>Bottom</button>
			
				<div className={"h-separator"} />
				<button className={'fsbl-logger-button'} type="button" onClick={this.showRawJSON}>Raw View</button>
				<span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
				<label className={'modeStyle'}>Depth</label>
				<input className={'inputStyle5'} type="number" value={this.state.viewDepth} onChange={this.viewDepth} />
				<div className={"h-separator"} />
				<button className={'fsbl-logger-button'} type="button" onClick={this.closeChildren}>X Child</button>
				
				<h1></h1>
			</div>
		</div>
	}
};

module.exports = LoggerClientControlList;


