// Finsemble Central Logger Service -- the top-level file

var baseService = require("../../baseService");
var util = require('../../../common/util');
var console = new util.Console('LoggerService');
var ConfigClient = require("../../../clients/configClient");

var React = require("react");
var ReactDOM = require("react-dom");
var ReactLogger = require("./components/ReactLogger");

function LoggerService() {
	var savedInitDoneCallback;

	function startLogger() {
		signalInitDone();
	}

	function signalInitDone() {
		savedInitDoneCallback();
	}

	this.initialize = function (initDoneCallback) {
		savedInitDoneCallback = initDoneCallback;

		function shutdownComponents () {
			return new Promise(function (resolve, reject) {
				console.log("Logger Shutting Down");
			});
		};
		this.onShutdown(shutdownComponents);
		startLogger();
	};
}

LoggerService.prototype = new baseService('authenticationService');
var loggerService = new LoggerService();

loggerService.onBaseServiceReady(function (callback) {
	console.log("onBaseServiceReady called");

	loggerService.initialize(function () {
		console.log("logger service initialized");
		callback();
	});

	let finWindow = fin.desktop.Window.getCurrent();
	finWindow.addEventListener('close-requested', function () {
		finWindow.hide();
	});


	this.RouterClient.addListener("CentralConsole-Show", function () {
		finWindow.show();
	});


	ReactDOM.render(
		<ReactLogger />,
		document.getElementById('Logger-component-wrapper')
	);
});

loggerService.start();



