var React = require("react");
const {Table, Column, Cell} = require('fixed-data-table-2');
const CSS = require("fixed-data-table-2/dist/fixed-data-table.css");
const HIGHLIGH_STYLE_COLOR = ["red", "blue", "green", "purple"];

var LauncherClient = require('../../../../clients/launcherClient');
LauncherClient.initialize();

var LoggerStore = require("../stores/LoggerStore")

// table component for display Log data in rows and columns; also invokes popup windows for viewing JSON data kept in the table;
// Based on second version of FaceBook's fixed-data-table: see https://github.com/schrodinger/fixed-data-table-2
class LoggerMainConsoleTable extends React.Component {
	constructor(props) {
		super(props);
		self = this;
		this.throttleTimer = null;
		this.queuedThrottle = false;
		this.update = false;
		this.state = {
			myTableData: LoggerStore.getCurrentLog(),
			dataColumnWidths: 8000,
			tableWidth: LoggerStore.getWindowWidth() - 50,
			tableHeight: LoggerStore.getWindowHeight() - 300,
			highLightArray1: LoggerStore.getHighlightArray1(),
			highLightArray2: LoggerStore.getHighlightArray2(),
			highLightArray3: LoggerStore.getHighlightArray3(),
			highLightArray4: LoggerStore.getHighlightArray4(),
			jumpToLocation: false
		};
		this.firstMessageTime = LoggerStore.getFirstMessageTime();
		this.clickwin = null;
		this.clickwinList = [];
		this.viewDepth = LoggerStore.getViewDepth();
		this.tableLocation = 0;

		this.onRowClickAction = this.onRowClickAction.bind(this);
		this.loggerGetNewMessages = this.loggerGetNewMessages.bind(this);
		this.getNewHightlightArrayValues1 = this.getNewHightlightArrayValues1.bind(this);
		this.getNewHightlightArrayValues2 = this.getNewHightlightArrayValues2.bind(this);
		this.getNewHightlightArrayValues3 = this.getNewHightlightArrayValues3.bind(this);
		this.getNewHightlightArrayValues4 = this.getNewHightlightArrayValues4.bind(this);
		this.closeClickChildren = this.closeClickChildren.bind(this);
		this.showRawJSON = this.showRawJSON.bind(this);
		this.windowResize = this.windowResize.bind(this);
		this.setTop = this.setTop.bind(this);
		this.setBottom = this.setBottom.bind(this);
		this.setViewDepth = this.setViewDepth.bind(this);
		this.throttle = this.throttle.bind(this);
	}
	
	componentWillMount() {
		LoggerStore.addEmitterListener("loggerNewMessages", this.loggerGetNewMessages);
		LoggerStore.addEmitterListener("newShowClientState", this.loggerGetNewMessages);
		LoggerStore.addEmitterListener("NewHighlightValues1", this.getNewHightlightArrayValues1);
		LoggerStore.addEmitterListener("NewHighlightValues2", this.getNewHightlightArrayValues2);
		LoggerStore.addEmitterListener("NewHighlightValues3", this.getNewHightlightArrayValues3);
		LoggerStore.addEmitterListener("NewHighlightValues4", this.getNewHightlightArrayValues4);
		LoggerStore.addEmitterListener("closeDisplayChildren", this.closeClickChildren);
		LoggerStore.addEmitterListener("showRawJSON", this.showRawJSON);
		LoggerStore.addEmitterListener("WindowResize", this.windowResize);
		LoggerStore.addEmitterListener("SetTop", this.setTop);
		LoggerStore.addEmitterListener("SetBottom", this.setBottom);
		LoggerStore.addEmitterListener("NewViewDepth", this.setViewDepth);
	}
	
	componentWillUnmount() {
		LoggerStore.removeEmitterListener("loggerNewMessages", this.loggerGetNewMessages);
		LoggerStore.removeEmitterListener("newShowClientState", this.loggerGetNewMessages);
		LoggerStore.removeEmitterListener("NewHighlightValues1", this.getNewHightlightArrayValues1);
		LoggerStore.removeEmitterListener("NewHighlightValues2", this.getNewHightlightArrayValues2);
		LoggerStore.removeEmitterListener("NewHighlightValues3", this.getNewHightlightArrayValues3);
		LoggerStore.removeEmitterListener("NewHighlightValues4", this.getNewHightlightArrayValues4);
		LoggerStore.removeEmitterListener("closeDisplayChildren", this.closeClickChildren);
		LoggerStore.removeEmitterListener("showRawJSON", this.showRawJSON);
		LoggerStore.removeEmitterListener("SetTop", this.setTop);
		LoggerStore.removeEmitterListener("SetBottom", this.setBottom);
		LoggerStore.removeEmitterListener("NewViewDepth", this.setViewDepth);
	}

	componentDidUpdate() {
		this.state.jumpToLocation = false;
	}

	shouldComponentUpdate(nextProps, nextState) {
		var update = this.update;
		if (this.update) {
			this.update = false;
		}
		return update;
	}
	
	getNewHightlightArrayValues1() {
		var newArray = LoggerStore.getHighlightArray1();
		this.update = true;
		this.setState({ highLightArray1: newArray });
	}

	getNewHightlightArrayValues2() {
		var newArray = LoggerStore.getHighlightArray2();
		this.update = true;
		this.setState({ highLightArray2: newArray });
	}

	getNewHightlightArrayValues3() {
		var newArray = LoggerStore.getHighlightArray3();
		this.update = true;
		this.setState({ highLightArray3: newArray });
	}

	getNewHightlightArrayValues4() {
		var newArray = LoggerStore.getHighlightArray4();
		this.update = true;
		this.setState({ highLightArray4: newArray });
	}

	throttle(callback) { // don't update more often than THROTTLE_TIME...the rendering severly drags down performance
		const THROTTLE_TIME = 1500;
		if (!this.throttleTimer) {
			this.throttleTimer = setTimeout(function () {
				self.throttleTimer = null;
				if (self.queuedThrottle) { // always do one last callback if any were skipped
					if (callback) { 
						callback();
					}
				}
			}, THROTTLE_TIME);
			if (callback) {
				callback();
			}
		} else {
			this.queuedThrottle = true; // remember if at least one callback skipped
		}
	}

	loggerGetNewMessages() {
		this.throttle(function () {
			var theLog = LoggerStore.getCurrentLog();
			self.firstMessageTime = LoggerStore.getFirstMessageTime();
			self.update = true;
			self.setState({ myTableData: theLog });

			self.getNewHightlightArrayValues1();
			self.getNewHightlightArrayValues2();
			self.getNewHightlightArrayValues3();
			self.getNewHightlightArrayValues4();
		});	
	}

	typeStyle(type) {
		var style;
		if (type == "Error") {
			style = { color: 'red' };
		} else if (type == "Warn") {
			style = { color: '#D4A017' };
		} else {
			style = { color: 'black' };
		}
		return style;
	}

	highLight1Style(flag) {
		var style;
		if (flag) {
			style = { backgroundColor: HIGHLIGH_STYLE_COLOR[0] };
		} else {
			style = { backgroundColor: 'white' };
		}
		return style;
	}

	highLight2Style(flag) {
		var style;
		if (flag) {
			style = { backgroundColor: HIGHLIGH_STYLE_COLOR[1] };
		} else {
			style = { backgroundColor: 'white' };
		}
		return style;
	}

	highLight3Style(flag) {
		var style;
		if (flag) {
			style = { backgroundColor: HIGHLIGH_STYLE_COLOR[2]};
		} else {
			style = { backgroundColor: 'white' };
		}
		return style;
	}
	
	highLight4Style(flag) {
		var style;
		if (flag) {
			style = { backgroundColor: HIGHLIGH_STYLE_COLOR[3] };
		} else {
			style = { backgroundColor: 'white' };
		}
		return style;
	}

	closeClickChildren() {
		for (var i = 0; i < this.clickwinList.length; i++) {
			let thisClickWin = this.clickwinList[i];
			thisClickWin.close();
		}
		this.clickwinList = [];
	}

	onRowClickAction(event, index) {
		var theLog = LoggerStore.getCurrentLog();
		var logData = theLog[index];
		var viewDepth = this.viewDepth;
		LauncherClient.spawn("JSONFormatter", { data: { index, logData, viewDepth } }, function (err, msg) {
			if (err) {
				console.error("Failed To JSONFormatter: " + JSON.stringify(err));
			} else {
				self.clickwinList.push(msg.finWindow);
			}
		});
	}

	showRawJSON(event, index) {
		var theLog = LoggerStore.getCurrentLog();
		var logData = theLog;
		var highlightString = LoggerStore.getHighlightStrings();
		var viewDepth = this.viewDepth;
		LauncherClient.spawn("JSONAllData", { data: { highlightString, logData, startIndex: 0, stopIndex: logData.length-1, viewDepth } }, function (err, msg) {
					window.console.info("JSONAllData spawned", msg);
					if (err) {
						console.error("Failed To JSONAllData: " + JSON.stringify(err));
					} else {
						self.clickwinList.push(msg.finWindow);
					}
		});	}

	windowResize() {
		var newTableWidth = LoggerStore.getWindowWidth() - 50;
		var newTableHeight = LoggerStore.getWindowHeight() - 150;
		this.setState({ tableWidth: newTableWidth});
		this.setState({ tableHeight: newTableHeight});
	}

	setTop() {
		this.setState({ jumpToLocation: true}); // cleared in componentDidUpdate 
		this.tableLocation = 0;
		this.update = true;
	}

	setBottom() {
		this.setState({ jumpToLocation: true}); // cleared in componentDidUpdate 
		var counts = LoggerStore.getLogCounts();
		this.tableLocation = counts.display;
		this.update = true;
	}

	setViewDepth() {
		this.viewDepth = LoggerStore.getViewDepth();
	}

	processArg(data) {
		return data;
	}

	render() {
		const searchStyle = { color: "red" };
		const highLightColumnWidth = 3;

		return (
			<Table
			onRowClick={this.onRowClickAction}	
				rowsCount={this.state.myTableData.length}
			scrollToRow={this.state.jumpToLocation ? this.tableLocation : undefined }	
			isColumnResizing={false}	
			rowHeight={22}
			headerHeight={35}
			width={this.state.tableWidth} 
			height={this.state.tableHeight}>
			<Column
				header={<Cell></Cell>}
				cell={props => (
				<Cell {...props} style={this.highLight1Style(this.state.highLightArray1[props.rowIndex])}>
					{props.rowIndex}
				</Cell>
				)}
				width={highLightColumnWidth}
			/>
			<Column
				header={<Cell></Cell>}
				cell={props => (
					<Cell {...props} style={this.highLight2Style(this.state.highLightArray2[props.rowIndex])}>
					{props.rowIndex}
				</Cell>
				)}
				width={highLightColumnWidth}
			/>
			<Column
				header={<Cell></Cell>}
				cell={props => (
					<Cell {...props} style={this.highLight3Style(this.state.highLightArray3[props.rowIndex])}>
					{props.rowIndex}
				</Cell>
				)}
				width={highLightColumnWidth}
			/>
			<Column
				header={<Cell></Cell>}
				cell={props => (
					<Cell {...props} style={this.highLight4Style(this.state.highLightArray4[props.rowIndex])}>
					{props.rowIndex}
				</Cell>
				)}
				width={highLightColumnWidth}
			/>
			<Column
				header={<Cell>#</Cell>}
				cell={props => (
				<Cell {...props}>
					{props.rowIndex + 1}
				</Cell>
				)}
				width={65}
				minWidth={100}
				maxwidth={500}
			/>	
			<Column
				header={<Cell>Component</Cell>}
				cell={props => (
				<Cell {...props}>
					{this.state.myTableData[props.rowIndex].logClientName}
				</Cell>
				)}
				width={150}
				minWidth={100}
				maxwidth={500}
			/>
			<Column
				header={<Cell>Mode</Cell>}
				cell={props => (
				<Cell {...props}>
						{this.state.myTableData[props.rowIndex].category}
				</Cell>
				)}
				width={70}
			/>
			<Column
				header={<Cell>Type</Cell>}
				cell={props => (
				<Cell {...props} style={this.typeStyle(this.state.myTableData[props.rowIndex].logType)}>
						{this.state.myTableData[props.rowIndex].logType}
				</Cell>
				)}
				width={60}
			/>	
			<Column
				header={<Cell>Time</Cell>}
				cell={props => (
				<Cell {...props}>
					{Math.round(this.state.myTableData[props.rowIndex].logTimestamp - this.firstMessageTime)} 
				</Cell>
				)}
				width={57}
			/>	
			<Column
				columnKey="arg1"		
				header={<Cell>Console Data Arguments</Cell>}
				cell={props => (
					<Cell {...props} style={{ font: "Roboto" }}>
						{this.processArg(this.state.myTableData[props.rowIndex].allLogArgs)}
					</Cell>
				)}
				width={this.state.dataColumnWidths}
				minWidth={200}
				maxwidth={10000}
				isResizable={true}
			/>
			</Table>
		);
	}
}

module.exports = LoggerMainConsoleTable;


