var React = require("react");
var Select = require("react-select");
var TextArea = require('react-text-input').TextArea;
const CSS = require("react-select/dist/react-select.css");

var LoggerStore = require("../stores/LoggerStore")

// once class per client registered with logger; primarily this components provides UI affecting communication with the client
class LoggerClientControl extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			clientState: LoggerStore.getClientState(this.props.clientName),
			showClientState: LoggerStore.getShowClientState(this.props.clientName),
			hideInactive: LoggerStore.getHideInactiveState(),
			fileMode: this.props.fileMode,
			clientMode: LoggerStore.getClientMode()
		};

		this.showClientToggle = this.showClientToggle.bind(this);
		this.allToggle = this.allToggle.bind(this);
		this.errorsToggle = this.errorsToggle.bind(this);
		this.warningsToggle = this.warningsToggle.bind(this);
		this.infoToggle = this.infoToggle.bind(this);
		this.logsToggle = this.logsToggle.bind(this);
		this.debugToggle = this.debugToggle.bind(this);
		this.verboseToggle = this.verboseToggle.bind(this);
		this.expandObjectsToggle = this.expandObjectsToggle.bind(this);
		this.localOnlyToggle = this.localOnlyToggle.bind(this);
		this.getNewClientState = this.getNewClientState.bind(this);
		this.getNewShowClientState = this.getNewShowClientState.bind(this);
		this.getHideInactiveState = this.getHideInactiveState.bind(this);
		this.fileMode = this.fileMode.bind(this);
		this.changeClientMode = this.changeClientMode.bind(this);
	}

	componentWillMount() {
		LoggerStore.addEmitterListener("newClientState", this.getNewClientState);
		LoggerStore.addEmitterListener("newShowClientState", this.getNewShowClientState);
		LoggerStore.addEmitterListener("newHideInactiveState", this.getHideInactiveState);
		LoggerStore.addEmitterListener("FileMode", this.fileMode);
		LoggerStore.addEmitterListener("ChangeClientMode", this.changeClientMode);
	}

	componentWillUnmount() {
		LoggerStore.removeEmitterListener("newClientState", this.getNewClientState);
		LoggerStore.removeEmitterListener("newShowClientState", this.getNewShowClientState);
		LoggerStore.removeEmitterListener("newHideInactiveState", this.getHideInactiveState);
		LoggerStore.removeEmitterListener("FileMode", this.fileMode);
		LoggerStore.removeEmitterListener("ChangeClientMode", this.changeClientMode);
	}

	fileMode() {
		var fileModeState = LoggerStore.getFileMode();
		this.setState({ fileMode: fileModeState });
	}

	getNewClientState() {
		var newClientState = LoggerStore.getClientState(this.props.clientName);
		this.setState({ clientState: newClientState });
	}

	getNewShowClientState() {
		var newShowClientState = LoggerStore.getShowClientState(this.props.clientName);
		this.setState({ showClientState: newShowClientState });
	}

	getHideInactiveState() {
		var newState = LoggerStore.getHideInactiveState();
		this.setState({ hideInactive: newState });
	}

	showClientToggle() {
		LoggerStore.toggleShowClient(this.props.clientName);
	}

	allToggle() {
		LoggerStore.toggleClientAll(this.props.clientName);
	}

	errorsToggle() {
		LoggerStore.toggleClientErrors(this.props.clientName);
	}

	warningsToggle() {
		LoggerStore.toggleClientWarnings(this.props.clientName);
	}

	infoToggle() {
		LoggerStore.toggleClientInfo(this.props.clientName);
	}

	logsToggle() {
		LoggerStore.toggleClientLogs(this.props.clientName);
	}

	debugToggle() {
		LoggerStore.toggleClientDebug(this.props.clientName);
	}

	verboseToggle() {
		LoggerStore.toggleClientVerbose(this.props.clientName);
	}

	expandObjectsToggle() {
		LoggerStore.toggleClientExpandObjects(this.props.clientName);
	}

	localOnlyToggle() {
		LoggerStore.toggleClientLocalOnly(this.props.clientName);
	}

	changeClientMode() {
		var newMode = LoggerStore.getClientMode();
		this.setState({ clientMode: newMode });
	}

	render() {

		return ( <div>
			{ this.state.hideInactive && !this.state.showClientState ? null :
					<div className={'divStyleGreen'}>
						<input type="checkbox" checked={this.state.showClientState} onChange={this.showClientToggle} />
						<div className={'clientStyle'}>{this.props.clientName}</div>
						{this.state.fileMode ? null :
							<span>
								<label className={'sepStyle'}></label>
								<button className={'allStyle'} type="button" onClick={this.allToggle}>Toggle Core</button>
								<label className={'paddingStyle'}>Errors</label>
								<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Error} onChange={this.errorsToggle} />
								<label className={'paddingStyle'}>Warning</label>
								<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Warn} onChange={this.warningsToggle} />
								<label className={'paddingStyle'}>Info</label>
								<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Info} onChange={this.infoToggle} />
								<label className={'paddingStyle'}>Logs</label>
								<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Log} onChange={this.logsToggle} />
								<label className={'paddingStyle'}>Debug</label>
								<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Debug} onChange={this.debugToggle} />
								{this.state.clientMode === "console" ? null :
									<span>
										<label className={'labelStyle'}></label>
										<label>Verbose</label>
										<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].Verbose} onChange={this.verboseToggle} />
										<label className={'labelStyle'}></label>
										<label>Local Console Only</label>
										<input className={'checkStyle'} type="checkbox" checked={this.state.clientState[this.state.clientMode].LocalOnly} onChange={this.localOnlyToggle} />
									</span>
								}
							</span>
						}
				</div>
			}
		</div>)
	}
};

module.exports = LoggerClientControl;


