var React = require("react");
var ReactDOM = require("react-dom");

var LoggerTopControl = require("./LoggerTopControl");
var LoggerClientControlList = require("./LoggerClientControlList");
var LoggerDisplayControl = require("./LoggerDisplayControl");
var LoggerMainConsoleTable = require("./LoggerMainConsoleTable");
var LoggerMainConsolePlain = require("./LoggerMainConsolePlain");
var LoggerStore = require("../stores/LoggerStore");

// parent component for all of central logger
class ReactLogger extends React.Component {
	constructor(props) {
		super(props);

		var logState = LoggerStore.getLogState();

		this.state = {
			plainTextConsole: LoggerStore.getPlainTextConsole(),
			fileMode: LoggerStore.getFileMode()
		};

		this.getPlainTextConsoleState = this.getPlainTextConsoleState.bind(this);
		this.fileMode = this.fileMode.bind(this);
	}

	componentWillMount() {
		LoggerStore.addEmitterListener("newConsoleState", this.getPlainTextConsoleState);
		LoggerStore.addEmitterListener("FileMode", this.fileMode);
	}

	componentWillUnmount() {
		LoggerStore.removeEmitterListener("FileMode", this.fileMode);
	}

	getPlainTextConsoleState() {
		var plainTextConsoleState = LoggerStore.getPlainTextConsole();
		this.setState({ plainTextConsole: plainTextConsoleState });
	}

	fileMode() {
		var fileModeState = LoggerStore.getFileMode();
		this.setState({ fileMode: fileModeState });
	}

	// currently plain-text mode is never used, but hook left in case needed in future
	render() {
		var self = this;
		return (<div>
				<LoggerTopControl/>
				<LoggerClientControlList fileMode={this.state.fileMode}/>
				<LoggerDisplayControl/>
				{this.state.plainTextConsole ? <LoggerMainConsolePlain/>: null }
				{ this.state.plainTextConsole ? null : <LoggerMainConsoleTable/>}
			</div>)
	}
};

module.exports = ReactLogger;


