var React = require("react");
var LoggerStore = require("../stores/LoggerStore")

// rought cut on component for raw table output.  Currently not used but may need to enhance if "View JSON" is too slow or limited
class LoggerMainConsolePlain extends React.Component {
	constructor(props) {
		super(props);
		self = this;
		this.update = false;
		this.state = {
			myTableData: LoggerStore.getCurrentLog()

		};
		this.loggerGetNewMessages = this.loggerGetNewMessages.bind(this);
	}
	
	componentWillMount() {
		LoggerStore.addEmitterListener(["loggerNewMessages"], this.loggerGetNewMessages);
		LoggerStore.addEmitterListener(["newShowClientState"], this.loggerGetNewMessages);
	}
	
	componentWillUnmount() {
		LoggerStore.removeEmitterListener(["loggerNewMessages"], this.loggerGetNewMessages);
		LoggerStore.removeEmitterListener(["newShowClientState"], this.loggerGetNewMessages);
	}

	shouldComponentUpdate(nextProps, nextState) {
		var update = this.update;
		if (this.update) {
			this.update = false;
		}
		return update;
	}
	
	loggerGetNewMessages() {
		var theLog = LoggerStore.getCurrentLog();
		this.update = true;
		this.setState({ myTableData: theLog });
	}

	typeStyle(type) {
		var style;
		if (type == "Error") {
			style = { color: 'red' };
		} else if (type == "Warn") {
			style = { color: '#D4A017' };
		} else if (type == "Debug") {
			style = { color: 'blue' };
		} else if (type == "Verbose") {
			style = { color: 'darkblue' };
		} else {
			style = { color: 'black' };
		}
		return style;
	}

	render() {
		const ulStyle = { WebkitPaddingStart: 0  };
		const dataStyle = { WebkitPaddingStart: 16  };
		
		var DataList = this.state.myTableData.map((name, index) => {
			return <div style={this.typeStyle(this.state.myTableData[index].logType)}  key={index}>
				{this.state.myTableData[index].logClientName}{": "}
				<span >{this.state.myTableData[index].logType}</span>
				{"Timestamp="}{this.state.myTableData[index].logTimestamp}
				<br/>
				<div style={dataStyle}>	
					{this.state.myTableData[index].logArgs[0]} {" "}
					{this.state.myTableData[index].logArgs[1]} {" "}
					{this.state.myTableData[index].logArgs[1]} {" "}
					{this.state.myTableData[index].logArgs[1]} {" "}
					{this.state.myTableData[index].logArgs[1]} {" "}
				</div>
			</div>
		});

		return <div>
			<hr/>
				<ul style={ulStyle}>
						{DataList}
				</ul>
			</div>
	}
}

module.exports = LoggerMainConsolePlain;


