var React = require("react");
var ReactDOM = require("react-dom");
var LoggerClientControl = require('./LoggerClientControl.jsx');
var LoggerStore = require("../stores/LoggerStore")

// high-level affects on central logger.  Buttons to invoke ClearData, Export, Import, and Save Settings. 
class LoggerTopControl extends React.Component {
	constructor(props) {
		super(props);

		var logState = LoggerStore.getLogState();
		
		this.state = {
			logCount: LoggerStore.getLogCounts()
		};

		this.getNewLogCounts = this.getNewLogCounts.bind(this);
	}

	componentWillMount() {
		LoggerStore.addEmitterListener("loggerNewMessages", this.getNewLogCounts);
	}

	componentWillUnmount() {
		LoggerStore.removeEmitterListener("loggerNewMessages", this.getNewLogCounts);
	}

	clearData() {
		LoggerStore.clearData();
	}
	
	saveToFile() {
		LoggerStore.saveToFile();
	}
	
	fileMode() {
		LoggerStore.fileMode();
	}

	persist() {
		LoggerStore.persist();
	}

	getNewLogCounts() {
		var counts = LoggerStore.getLogCounts();
		this.setState({ logCount: counts });
	}

	render() {

		return <div className="topDiv">
			<div style={{ float: "left" }} className={"lineCount"}><b>{this.state.logCount.display}</b> Log lines shown out of <b>{this.state.logCount.master}</b> total.</div>
				<div style={{float: "right"}}>
					<button className={'fsbl-logger-button'} type="button" onClick={this.clearData}>Clear Data</button>
					<button className={'fsbl-logger-button'} type="button" onClick={this.saveToFile}>Export Log to File</button>
					<button className={'fsbl-logger-button'} type="button" onClick={this.fileMode}>Inport Log From File</button>
					<button className={'fsbl-logger-button'} type="button" onClick={this.persist}>Save Settings</button>
				</div>
			</div>
	}
};

module.exports = LoggerTopControl;


